import React from 'react';

import ThreeBounce from 'better-react-spinkit/dist/ThreeBounce';

import Box from './Box';

const Loading = (props) => (
  <Box py="2em" mx="auto" align="center" {...props}>
    <ThreeBounce />
  </Box>
);

export default Loading;



