import React from 'react';
import Box from '../../components/Box';
import Cloud from '../../components/Cloud';

const locations = [
  {
    w: '20%',
    top: '30%',
    right: '-5%',
  },
  {
    w: '15%',
    top: '5%',
    left: '5%',
  },
  {
    w: '18%',
    top: '60%',
    left: '-5%',
  },
];

export default (props) => (
  <Box {...props}>
    {locations.map((location, index) => (
      <Cloud
        key={index}
        position="absolute"
        top={location.top}
        right={location.right}
        left={location.left}
        w={location.w}
      />
    ))}
  </Box>
);



