import React from 'react';

import get from 'lodash/get';

import Box from '../../../components/Box';
import Border from '../../../components/Border';
import Text from '../../../components/Text';
import Link from '../../../components/Link';
import BackgroundImage from '../../../components/BackgroundImage';

const Project = ({
  covers,
  description,
  fields,
  id,
  name,
  published_on,
  stats,
  tags,
  url,
  ...props
}) => {
  return (
    <Border w={1} mb="1em" border="2px solid" borderColor="lightblue" {...props}>
      <Link href={url} target="_blank">
        <BackgroundImage src={get(covers, 404)} ratio={1} />
        <Text pt="1em" px="1em">
          {name}
        </Text>
        {/* <Border w="1em" ml="1em" pb="1em" borderTop="1px solid" borderColor="lightblue" /> */}
      </Link>
    </Border>
  );
};

export default Project;



