import React from 'react';
import styled from 'styled-components';
import { themeGet } from 'styled-system';

import FloorButton from '../Elevator/FloorButton';
import floors from '../Elevator/floors.json';

import Box from '../../components/Box';
import Link from '../../components/Link';
import Flex from '../../components/Flex';

const StyledLink = styled(Link)`
  &:hover {
    .elevator-button {
      background: ${themeGet('colors.yellow')};
    }
  }
`;

export default ({ currentFloor }) => (
  <Box my="1em">
    <Flex justify="center" align="center">
      {floors.map((f) => (
        <StyledLink
          key={f.floor}
          to={`/portifolio/${f.floor}`}
        >
          <FloorButton mx="0.5em" label={f.floor} active={f.floor === currentFloor} />
        </StyledLink>
      ))}
      <StyledLink to="/portifolio">
        <FloorButton mx="0.5em" label="All" />
      </StyledLink>
    </Flex>
  </Box>
);



