import React, { PureComponent } from 'react';
import map from 'lodash/map';
import mapValues from 'lodash/mapValues';

import GoBackHome from '../../containers/GoBackHome';

import Container from '../../components/Container';
import Flex from '../../components/Flex';
import Box from '../../components/Box';
import Border from '../../components/Border';
import Text from '../../components/Text';
import Dropdown from '../../components/Dropdown';
import Button from '../../components/Button';

import Clouds from './Clouds';
import ElevatorButton from './ElevatorButton';
import ProjectGrid from './ProjectGrid';
import hoc from './hoc';
import banners from './banners';
import tags from './tags';

const tagsWithAll = mapValues(tags, ({ label, options }) => ({
  label,
  options: [{ value: 'all', label }, ...options],
}));

const motions = tags.categories.options[2].items;

class Portifolio extends PureComponent {
  render() {
    const { pathContext, filter, setFilter, resetFilter } = this.props;
    const data = banners[pathContext.floor];
    return (
      <Box py="3em">
        <GoBackHome />
        <Box position="relative" overflow="hidden">
          <Clouds />
          <Container>
            <Box is="object" data={data.img} mt="3em">
              {data.title && (
                <Text.h1>{data.title}</Text.h1>
              )}
              {data.sub && (
                <Text>{data.sub}</Text>
              )}
            </Box>
            <ElevatorButton currentFloor={pathContext.floor} />
          </Container>
        </Box>
        <Border py="0.5em" borderColor="lightblue" borderTop="2px solid" borderBottom="2px solid">
          <Container>
            <Flex justify="space-between" py="0.5em" flexDirection={['column', null, 'row']}>
              <Flex flex={1} flexWrap={['wrap', null, 'nowrap']}>
                {map(tagsWithAll, ({ options }, key) => (
                  <Dropdown
                    key={key}
                    w={[1 / 3, null, 1]}
                    px="0.5em"
                    options={options}
                    onChange={(option) => setFilter({ type: key, value: option.value })}
                    value={filter[key]}
                    disabled={key === 'motionFinesse' && motions.findIndex(({ value }) => value === filter.categories) === -1}
                  />
                ))}
              </Flex>
              <Box w={[1, null, '6em']} ml={[0, null, '2em']} align="center">
                <Button.transparent onClick={resetFilter}>
                  重新搜尋
                </Button.transparent>
              </Box>
            </Flex>
            <ProjectGrid filter={filter} floorKey={pathContext.key} currentFloor={pathContext.floor} />
          </Container>
        </Border>
      </Box>
    );
  }
}

export default hoc(Portifolio);



