import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import size from 'lodash/size';
import toPairs from 'lodash/toPairs';
import chunk from 'lodash/chunk';
import loFilter from 'lodash/filter';
import get from 'lodash/get';
import { compose } from 'redux';

import withResponsive from '../../../hoc/withResponsive';

import Flex from '../../../components/Flex';
import Box from '../../../components/Box';
import Loading from '../../../components/Loading';
import tags from '../../../services/tags.json';

import { selectFirebaseData } from '../../../services/firebase/selectors';
import Project from './Project';

const matchTag = (tags, toMatch) => toMatch === 'none' ? !tags : tags && tags.some((value) => value === toMatch);

class ProjectGrid extends PureComponent {
  render() {
    const { projects, filter, floorKey, currentFloor, browser } = this.props;
    if (!size(projects)) return <Loading />;
    const activeFilters = toPairs(filter).filter(([, value]) => value !== 'all');
    if (floorKey) {
      activeFilters.push(['floor', floorKey]);
    } else if (currentFloor === 'B1') {
      activeFilters.push(['floor', 'none']);
    }
    const perRow = browser.greaterThan.xs ? 4 : 2;
    const filteredProjects = loFilter(projects, ({ id }) => activeFilters.every(([key, value]) => matchTag(get(tags, [id, key]), value)));
    return (
      <Flex flexWrap="wrap" py="1em">
        {chunk(filteredProjects.reverse(), perRow).map((group, index) =>
          <Flex w={1} key={index}>
            {group.map((data) => (
              <Box key={data.id} w={1 / perRow} p="0.5em">
                <Project height="100%" {...data} />
              </Box>
          ))}
          </Flex>
        )}
      </Flex>
    );
  }
}

ProjectGrid.propTypes = {
  projects: PropTypes.object,
  filter: PropTypes.object,
  floorKey: PropTypes.string,
};

export default compose(
  selectFirebaseData(['projects']),
  withResponsive
)(ProjectGrid);



