import b1 from './b1-banner.svg';
import all from './all-banner.svg';

export default {
  '1': {
    img: f1,
    title: '有趣無價玩具店',
    sub: '價值換算價格的公式我們可能永遠弄不清楚，從產品、計畫到夢想，不如讓你來定價。',
  },
  '2': {
    img: f2,
    title: '在地生活到國民外交',
    sub: '先好好思考自己和社會、和這片土地的連結，才有可能一起走出去。',
  },
  '3': {
    img: f3,
    title: '生態探索頻道',
    sub: '嘗試用不同的角度溝通，試著改變人和環境間的關係。',
  },
  '4': {
    img: f4,
    title: '資訊改造實驗室',
    sub: '改造生活中不友善的資訊設計，讓大家重新思考人和資訊間的關係。',
  },
  B1: {
    img: b1,
    title: '喝一口就不想走',
    sub: '好酒都在這，不同年份、各種釀酒師，若對品嚐有興趣，歡迎填表單留下訊息。',
  },
  all: {
    img: all,
  },
};



