import { fromJS } from 'immutable';
import { createAction, handleActions } from 'redux-actions';
import mapValues from 'lodash/mapValues';

import tags from './tags';

export const SET_FILTER = 'Portifolio/SET_FILTER';
export const RESET_FILTER = 'Portifolio/RESET_FILTER';

export const setFilter = createAction(SET_FILTER);
export const resetFilter = createAction(RESET_FILTER);

const initialState = fromJS(mapValues(tags, () => 'all'));

const reducer = handleActions({
  [SET_FILTER]: (state, { payload: { type, value } }) => state.set(type, value),
  [RESET_FILTER]: () => initialState,
}, initialState);

export default reducer;



